.chat_main{
    display: flex;
    flex-direction: column;
    flex: 0.75;
    background-color: #36393f;
    height: 100vh;
}

.chat_header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: lightgray;
    border-bottom: 1px solid #000000;
    padding: 20px;
}

.chat_header_channelName > h3{
    justify-content: space-between;
    align-items: center;
}

.chat_header_channelName > h3 > span{
    padding-right: 10px;
}

.chat_header_other{
    display: flex;
    flex: 0.4;
    align-items: center;
    justify-content: space-between;
}

.chat_header_other > input{
    position: relative;
    left: 15%;
    opacity: 0.3;
    border: none;
    line-height: 170%;
    width: 55%;
    border-radius: 5px;
}

.chatroom{
    flex: 1;
    color: lightgray;
    overflow: scroll;
    -ms-overflow-style: none;
    scrollbar-width: none;
    padding: 0 15% 0 20px;
}

.chatroom::-webkit-scrollbar{
    display: none;
}

.chat_footer{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: lightgray;
    background-color: #40444b;
    align-items: center;
    border-radius: 12px;
    flex-direction: row;
    justify-content: space-between;
    padding: 7px;
    margin: 0 15px 25px 15px;
}

.msg_input{
    flex: 1;
    border: none;
    background-color: transparent;
    color: lightgray;
    margin: 7px;
    font-size: medium;
}